<template>
  <div>
        <article class="tile is-child box">
              <h4 class="subtitle has-text-centered">DAILY SUMMARY SMS</h4>
              <h4 class="title has-text-centered"></h4>
        <br/>
        </article>
        <br/>
     <div class="tile is-ancestor">
      <div class="tile is-parent">
        <article class="tile is-child box">
          <div class="columns">
            <div class="column">
              <div class="field is-horizontal">
                <div class="columns">
                  <div class="column">
                    <label class="label">Pick Day</label>
                    <div class="control is-horizontal">
                    <datepicker :inputClass="{ test: true }" :config="{ wrap: true }" v-model="value.startDate">
                      <a class="button" data-toggle><i class="fa fa-calendar"></i></a>
                      <a class="button is-success" @click="loadDaily">Show</a>
                    </datepicker>
                    </div>
                  </div>
                </div>
                <hr/>
              </div>
            </div>
          </div>
          <div class="columns">
          <div class="column">
          <tabs alignment="centered" type="toggle">
                <tab-pane label="TELKOMSEL" selected>
                <h5 class="subtitle has-text-centered">Table SMS Summary</h5>
                <table class="table is-bordered is-striped is-narroww">
                  <thead class="has-text-centered">
                  <tr>
                    <th style="text-align: left">Operator</th>
                    <th style="text-align: center">Hour</th>
                    <th style="text-align: center">Delivered</th>
                    <th style="text-align: center">Failed</th>
                    <th style="text-align: center">Total Request</th>
                  </tr>
                  </thead>
                  <tbody>
                  <tr v-for="item in filterBy(items, 'TELKOMSEL SMS')">
                    <td class="">{{ item.operator }}</td>
                    <td class="has-text-centered">{{ item.count_time }}</td>
                    <td class="has-text-centered">{{ item.sms_request}}</td>
                    <td class="has-text-centered">{{ item.sms_failed}}</td>
                    <td class="has-text-centered">{{ item.sms_failed + item.sms_request}}</td>
                  </tr>
                  </tbody>
                </table>
                  </tab-pane>
                  <tab-pane label="INDOSAT">
                <h5 class="subtitle has-text-centered">Table SMS Summary</h5>
                <table class="table is-bordered is-striped is-narroww">
                  <thead class="has-text-centered">
                  <tr>
                    <th style="text-align: left">Operator</th>
                    <th style="text-align: center">Hour</th>
                    <th style="text-align: center">Delivered</th>
                    <th style="text-align: center">Failed</th>
                    <th style="text-align: center">Total Request</th>
                  </tr>
                  </thead>
                  <tbody>
                  <tr v-for="item in filterBy(items, 'INDOSAT SMS')">
                    <td class="">{{ item.operator }}</td>
                    <td class="has-text-centered">{{ item.count_time }}</td>
                    <td class="has-text-centered">{{ item.sms_request}}</td>
                    <td class="has-text-centered">{{ item.sms_failed}}</td>
                    <td class="has-text-centered">{{ item.sms_failed + item.sms_request}}</td>
                  </tr>
                  </tbody>
                </table>
                  </tab-pane>
                </tabs>
              </div>
          </div>
                        </article>
      </div>
    </div>
  </div>
</template>

<script>
  import Datepicker from 'vue-bulma-datepicker'
  import Chart from 'vue-bulma-chartjs'
  import _ from 'lodash'
  import moment from 'moment'
  import { Tabs, TabPane } from 'vue-bulma-tabs'

  export default {
    components: {
      Datepicker,
      Chart,
      Tabs,
      TabPane
    },

    data () {
      return {
        items: [{
          operator: null,
          count_time: null,
          sms_request: null,
          sms_failed: null
        }],
        datas: [{
          operator: null,
          count_date: null,
          sms_request: null,
          sms_failed: null
        }],
        month: null,
        months: ['Januari', 'Februari', 'Maret', 'April', 'Mei', 'Juni', 'Juli', 'Agustus', 'September', 'Oktober', 'November', 'Desember'],
        // ``data: {
        // ``  username: null,
        // ``  sms_request: 0,
        // ``  sms_success: 0,
        // ``  sms_failed: 0
        // ``},
        value: {
          startDate: '2017-24-03',
          endDate: null
        }
      }
    },

    // watch: {
    //   value (newVal, oldVal) {
    //     console.log(newVal, oldVal)
    //   }
    // },

    computed: {
      orderedUsers: function () {
        return _.orderBy(this.items, 'operator')
      }
    },

    methods: {
      loadDaily () {
        this.$http({
          url: 'http://5.189.176.218:6680/graph/daily',
          method: 'post',
          data: {
            startDate: this.value.startDate
          }
        }).then((response) => {
          this.items = response.data.items
        }).catch((error) => {
          console.log(error)
        })
      },
      loadMonthly () {
        let postData = null
        for (let i = 0; i < this.months.length; i++) {
          if (this.month === this.months[i]) {
            postData = i + 1
            break
          }
        }
        if (postData === null) {
          postData = moment().format('MM')
        }
        this.$http({
          url: 'http://5.189.176.218:6680/graph/monthly',
          method: 'post',
          data: {
            month: postData
          }
        }).then((response) => {
          this.datas = response.data.items
        }).catch((error) => {
          console.log(error)
        })
      }
    },

    mounted () {
      let hari = new Date()
      let yyyy = hari.getFullYear().toString()
      let mm = (hari.getMonth() + 1).toString()
      let dd = hari.getDate().toString()
      let mmChars = mm.split('')
      let ddChars = dd.split('')
      this.value.startDate = yyyy + '-' + (mmChars[1] ? mm : '0' + mmChars[0]) + '-' + (ddChars[1] ? dd : '0' + ddChars[0])
      this.value.monthDate = yyyy + '-' + (mmChars[1] ? mm : '0' + mmChars[0])
      this.loadDaily()
      this.loadMonthly()
      // this.$http({
      //   url: 'http://5.189.176.218:6680/graph/daily',
      //   method: 'post',
      //   data: {
      //     startDate: this.value.startDate
      //   }
      // }).then((response) => {
      //   this.items = response.data.items
      // }).catch((error) => {
      //   console.log(error)
      // })
    }
  }
</script>

<style lang="scss" scoped>
</style>



